.container{
  padding-bottom: 100px;
}
.nav{
  display: flex;
  justify-content: center;
  background-color: #252222;
  color: #575756;
  align-items: center;
}
.nav>.item,.nav>.activite,.logo{
  box-sizing: border-box;
  padding: 20px;
  cursor: pointer;
}
.nav>.item:hover{
  background-color: #000;
}
.activite{
  background-color: #000;
  color: #fff;
}
.loginBtn{
  margin-left: 10px;
}
.loginBtn:hover{
  color: #fff;
  cursor: pointer;
}
.logo{
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}
.audioPlayer{
  width: 100%;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  height: 80px;
  /* line-height: 80px; */
  text-align: center;
  filter: drop-shadow(2px 4px 6px black);
  display: flex;
  justify-content: center;
  align-items: center;
}
/* .audioPlayer>div{
  display: inline-block;
} */
.playerImg{
  width: 40px;
  height: 40px;
}
.playerName{
  width: 80px;
  height: 25px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  font-family: cursive;
}
.nextPrev{
  display: flex;
}
.prevBtn{
  font-size: 25px;
}
.nextBtn{
  font-size: 26px;
}
.prevBtn:hover,.nextBtn:hover{
  color: grey;
  cursor: pointer;
}
.disabled{
  cursor: no-drop;
}