.searchContainer{
    width: 1080px;
    margin: auto;
    padding-top: 50px;
}
.hotContent {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.hotItem {
    width: 22%;
}

.imgBox {
    position: relative;
    cursor: pointer;
}

.numStart {
    width: 100%;
    position: absolute;
    bottom: 3px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    font-family: cursive;
}

.num {
    margin: 5px 0px 5px 10px;
    color: #eee;
    display: flex;
    align-items: center;

}

.start {
    margin-right: 10px;
}

.imgBox img {
    width: 100%;
}
.imgBox .imgBg {
    position: absolute;
    width: 110%;
    left: -5%;
    top: -5%;
    filter: blur(10px);
    z-index: -1;
}

.player {
    cursor: pointer;
}
.player2{
    position: absolute;
    bottom: 8px;
    right: 5px;
    display: none;
    cursor: pointer;
}
.imgBox2:hover .player2{
    display: block;
}
.player:hover {
    color: grey !important;
}

.musicName {
    margin-top: 5px;
    font-size: 14px;
    width: 100%;
    font-family: cursive;
    overflow: hidden;
    text-overflow: ellipsis;
    /* 超出部分省略号 */
    word-break: break-all;
    /* break-all(允许在单词内换行。) */
    display: -webkit-box;
    /* 对象作为伸缩盒子模型显示 */
    -webkit-box-orient: vertical;
    /* 设置或检索伸缩盒对象的子元素的排列方式 */
    -webkit-line-clamp: 2;
    /* 显示的行数 */
    height: 60px;
    /* 设置最大高度，根据行高，要几行乘以几倍 */
}